<template>
  <section class="container" v-if="teacherDetails.data">
    <!-- 主页介绍 -->
    <div class="tutor space">
      <el-row :gutter="20">
        <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
          <div class="avatar">
            <img :src="teacherDetails.data.info.list_cover"/>
          </div>
        </el-col>
        <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16" class="df dfdir dfjbw teacherInfo">
          <div class="basicInfo">
            <p class="name">{{teacherDetails.data.info.name}}</p>
            <p class="description">{{teacherDetails.data.info.job}}</p>
            <p class="tags">
                        <span v-for="catalog in teacherDetails.data.types">
                          {{catalog.catalog_type_name}}
                        </span>
            </p>
          </div>
          <div class="contact df dfaic">
            <span class="invite" @click="askForClass">邀请老师授课</span>
            <div class="touch df dfdir dfaic">
              联系老师助理
              <p class="phone">
                <span class="iconfont phoneIcon"></span>
                {{teacherDetails.data.info.contact}}
              </p>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>

    <div class="modular space">
      <el-row :gutter="10">
        <el-col :xs="24" :sm="24" :md="18" :lg="18" :xl="18">
          <div class="vessels">
            <Modular :modeInfo="baseInfo" v-if="baseInfo.content" type="1"/>
            <Modular :modeInfo="topicInfo" v-if="topicInfo.list&&topicInfo.list.length>0" type="2"/>
            <Modular :modeInfo="evaluateInfo" v-if="evaluateInfo.list&&evaluateInfo.list.length>0" type="6"/>
            <Modular :modeInfo="sceneInfo" v-if="sceneInfo.list&&sceneInfo.list.length>0" type="3"/>
          </div>
          <div class="position"></div>
        </el-col>
        <!-- <el-col :xs="24" :sm="24" :md="6" :lg="6" :xl="6">
            <h3 class="title">推荐老师</h3>
            <div class="tList">
                <div class="character">
                    <p class="photoShow" style='backgroundImage:url("https://sslfile.ichazuo.cn/1480920120bb1443cc31d7396bf73e7858cea114e1.jpg")'></p>
                    <div class="desc">
                        <p class="name">
                            倪建伟
                            <span v-if="true">销售类</span></p>
                        <div class="ellipsis2 info">
                            <p>服务过华为、海尔、腾讯、 苏宁、中国移动等100+企业</p>
                        </div>
                    </div>
                </div>
            </div>
        </el-col> -->

      </el-row>
    </div>
  </section>
</template>
<script>
  import {mapState, mapActions} from 'vuex'
  import merge from 'webpack-merge'
  import Modular from '../../../components/Modular'

  export default {
    data() {
      return {
        baseInfo: {},
        topicInfo: {},
        evaluateInfo: {},
        sceneInfo: {}
      }
    },
    computed: {
      ...mapState({
        teacherDetails: state => state.teachers.teacherDetails
      }),
    },
    created() {
      const {id} = this.$route.params;
      document.title = `讲师详情 - 插座学院`
      // 获取讲师详细信息
      this.getTeacherDetails(id);
    },
    methods: {
      ...mapActions({
        getTeacherInfo: 'teachers/getTeacherInfo'
      }),

      async getTeacherDetails(id){
        const res = await this.getTeacherInfo({teacherId: id});
        const {catalogs, comments, imgs, info, types} = res.data;
        this.baseInfo = {
          title: "讲师介绍",
          content: info.content
        }
        this.topicInfo = {title: "课程主题", list: catalogs, topic: info.topic}
        this.evaluateInfo = {title: "客户评价", list: comments}
        this.sceneInfo = {title: "授课现场", list: imgs}
      },

      /** 邀请老师上课 */
      askForClass(){
        console.log(this.teacherDetails);
      },

      /**
       * 路由跳转
       * @param path 路由地址
       */
      toPath(path) {
        this.$router.push(path)
      },
    },
    components: {
      Modular
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .container {
    width: 75%;
    margin: 0 auto;
    overflow: hidden;
    min-height: 100vh;
    margin-bottom: -295px;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 380px;
    }

    .space {
      margin-top: 24px;

    }

    .tutor {
      background-color: #fff;
      padding: 50px 32px;
      .avatar {
        position: relative;
        width: 260px;
        height: 308px;
        margin: 0 auto 24px;
        background-image: linear-gradient(to bottom, #F6EADE 0%, #F5DEC9 40%, #866F5B 100%);
        margin: 0 auto 24px;
        img {
          width: 100%;
          vertical-align: middle;
        }
      }
      .teacherInfo {
        min-height: 308px;
        .basicInfo {
          .name {
            margin-bottom: 20px;
            font-size: 40px;
            color: #333;
          }
          .description {
            margin-bottom: 20px;
            font-size: 22px;
            color: #727272;
          }
          .tags {
            overflow: hidden;
            margin: 14px -8px;
            span {
              float: left;
              margin: 8px;
              padding: 0 16px;
              line-height: 36px;
              font-size: 14px;
              color: #06609A;
              background: #E6EFF4;
              border-radius: 18px;
            }
          }

        }

        .contact {
          .invite {
            width: 284px;
            line-height: 64px;
            text-align: center;
            background: #06609A;
            border-radius: 32px;
            color: #fff;
            font-size: 24px;
            margin-right: 24px;
            cursor: pointer;
          }
          .touch {
            font-size: 18px;
            line-height: 1.6;
            color: #06609A;
            .phone {
              font-size: 28px;
              .phoneIcon {
                font-weight: 700;
              }
            }
          }
        }
      }
    }

    .modular {
      .vessels {
        background: #fff;
      }

      .position {
        // background: #fff;
        height: 80px;
      }

      .title {
        font-size: 24px;
        color: #666;
        margin: 16px 0 12px;
      }
      .tList {
        .character {
          padding: 20px;
          background: #fff;
          box-shadow: 3px 4px 8px #f7f7f7;
          position: relative;
          overflow: hidden;
          cursor: pointer;
          border-radius: 6px;
          .photoShow {
            width: 100%;
            height: 218px;
            display: block;
            border-radius: 8px;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
          }
          .desc {
            width: 100%;
            background: #fff;
            padding: 24px 20px;
            box-sizing: border-box;
            .name {
              font-size: 24px;
              color: #06609A;
              span {
                padding: 2px 6px;
                margin-left: 8px;
                background: #E2AD87;
                color: #80624C;
                border-radius: 4px;
                font-size: 12px;
              }
            }
            .info {
              font-size: 14px;
              color: #666;
              line-height: 2;
              margin-top: 8px;
              .goodAt {
                margin-top: 10px;
              }
            }
          }
        }
      }

    }
  }

  .page {
    padding: 32px 0;
    text-align: center;
  }

  @media screen and (min-width: 200px) and (max-width: 768px) {
    .container {
      box-sizing: border-box;
      display: flex;
      width: 100%;
      overflow: hidden;
      flex-direction: column;
    }

    .sidebar {
      box-sizing: border-box;
      width: auto;
      flex: 1;
      margin-left: 0;
      margin-top: 24px;
    }
  }
</style>



